import {ajax} from '../utils/main'
export default {
  state: {
    current: {
      name: '',
      token: '',
      email: ''
    }
  },
  mutations: {

  },
  actions: {
    login (context) {
      return ajax({
        url: '/users/login',
        method: 'post'
      })
    }
  }
}



